.main-container {
    min-height: 100vh;
    /* background-image: url("../../assets/ContactMe/contact_us.jpeg"); */

    margin: 100px 0 -450px 0;
}

.central-form {
    max-width: 900px;
    padding: 15px;
    display: flex;
    box-shadow: 0 0px 15px -2px #003c63;
    border-radius: 20px;
    margin: 0 auto;
    flex-direction: column;
    justify-content: center;
    background-color: #6a92ad6c;
}

.central-form h2 {
    color: #0f0d0d;
    font-family: "Poppins Bold";
    letter-spacing: 0.2rem;
    margin-bottom: 20px;
}

