.central-form form {
    width: 800px;
    /* box-shadow: 0 0 2px 1px white; */
    display: flex;
    color: #111;
    padding: 20px;
    justify-content: center;
    flex-direction: column;
    margin-top: -40px;
    margin-left: 75px;
    opacity: 0.9;
    background-color: white;
    /* box-shadow: 0 0px 20px -2px #1f2235; */
    border-radius: 20px;
}

form p {
    text-align: center;
    margin-bottom: 10px;
    color: #ff5823;
    letter-spacing: 0.1rem;
    font-family: "Poppins Bold";
}

form label {
    margin-bottom: 5px;
    color: #111;
    letter-spacing: 0.2rem;
}

form input,
textarea {
    width: auto;
    border: 2px solid rgba(0, 0, 0, 0);
    outline: none;
    background-color: rgba(230, 230, 230, 0.6);
    padding: 0.5rem 1rem;
    font-size: 1.1rem;
    margin-bottom: 22px;
    transition: 0.3s;
    border-radius: 10px;
}

textarea:focus {
    border: 2px solid rgba(30, 85, 250, 0.47);
    background-color: #fff;
}
form input:focus {
    border: 2px solid rgba(30, 85, 250, 0.47);
    background-color: #fff;
}

form button {
    margin-top: 15px;
    padding: 5px;
    outline: none;
    width: 900px;
    color: rgb(230, 227, 227);
    font-size: 12px;
    border-radius: 19px;
    background-color: #003c63;
    font-size: 18px;
    width: 160px;
    align-items: center;
    justify-content: center;
}

form button:hover {
    background-color: #003c63;
    border: 2px solid #ff5823;
}

button .load {
    width: 100%;
}

button .load img {
    width: 60px;
    color: green;
}
.col i {
    padding: 7px;
    color: #fff;
}
.col i:hover {
    transform: scale(1.5);
}

@media screen and (min-width: 940px) {
    .back-form {
        max-width: 1100px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        margin-top: 30px;
    }

    /* .img-back {
        flex: 1;
        align-self: center;
        margin-right: 20px;
    } */
    form {
        flex: 0.9;
    }
}

@media screen and (max-width: 940px) {
    .main-container {
        padding: 0 10%;
    }
    .central-form form {
        width: 100%;
        margin-top: -10px;
        margin-left: 0px;
    }
}
@media screen and (max-width: 414px) {
    .central-form h2 {
        font-size: 16px;
    }
}
